                  </v-expansion-panel>
                </v-expansion-panels>
                <div v-else class="title">
                  {{ err }}
                </div>


              </v-card-text>
            </v-container>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
    </v-card>
    <v-snackbar centered top
                v-model="snackbar"
    >
      {{ err }}

      <template v-slot:action="{ attrs }">
        <v-btn
            color="red"
            text
            v-bind="attrs"
            @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
